import { HttpClient } from '@angular/common/http';
import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ChartDataSets } from 'chart.js';
import { Color, Label } from 'ng2-charts';
import { CryptoCoin } from 'src/app/models/crypto-coin';
import { SettingsPage } from 'src/app/pages/settings/settings.page';

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss'],
})
export class CardComponent implements OnInit {
  @Input() bitcoin$: CryptoCoin;
  @Input() selectedFiat: string;

  // private lineChart: any;
  // Data
  chartData: ChartDataSets[] = [{ data: [], label: 'Chart price' }];
  chartLabels: Label[];
 
  // Options
  chartOptions = {
    responsive: true,
    title: {
      display: true,
      text: 'chart price'
    },
    pan: {
      enabled: true,
      mode: 'xy'
    },
    zoom: {
      enabled: true,
      mode: 'xy'
    },
    scales: {
      xAxes: [{
        display: false
      }],
      yAxes: [{
        display: true
      }]
      }
  };
  chartColors: Color[] = [
    {
      borderColor: '#3dc2ff',
      backgroundColor: '#36abe0'
    }
  ];
  currentModal = null;
  // For search
  stock = '';
  modal : any;
  settings  = {
    chartType : 'line',
    showLegend : false
  };

  constructor(private http: HttpClient,public modalController: ModalController) {
  }

  ngOnInit() {
    
    console.log('this is it'+this.bitcoin$.chart);
    this.getData();
  }

  ionViewDidEnter (){
    
  }
  async presentModal() {
     this.modal = await this.modalController.create({
      component: SettingsPage,
      swipeToClose: true,
      componentProps: {
        settings: this.settings
      }
    });
    this.modal.onWillDismiss().then(dataReturned => {
      // trigger when about to close the modal
      this.settings = dataReturned.data;
      console.log('Receive: ', this.settings);
    });
    return await this.modal.present();
  }


  getData() {
   
    setTimeout(
      ()=>{
        this.chartLabels = [];
        this.chartData[0].data = [];

        for (let entry of this.bitcoin$.chart.prices) {
          this.chartLabels.push(entry[0].toString());
          this.chartData[0].data.push(entry[1]);
        }
      },2000
    )
  

}

  async updateChart(){
  const { data } = await this.modal.onWillDismiss();
  console.log(data);
}


}
